<template>
  <div class="translate" v-if="current">
    <h1>翻译练习
      <el-button size="mini" class="blue" @click="studyMode = !studyMode; showAns = false" type="text">
        {{ studyMode ? '切换到练习模式' : '切换到学习模式' }}
      </el-button>
    </h1>
    <p>第 {{currentIndex + 1}} / {{len}} 题</p>
    <p v-if="showLastIndex">上次学到第 <span class="blue">{{lastIndex + 1}}</span> 题
    <span class="blue" @click="proceed" type="text" v-if="lastIndex">继续学习 <i class="el-icon-arrow-right el-icon--right"></i></span></p>
    <p>{{studyMode ? current['eng-help'] : current.eng}}</p>
    <p class="green" v-if="percent && percent > 0.6">匹配度：{{ this.percent | showPercent }}</p>
    <p class="b" v-if="studyMode">{{current['zh-cn']}}</p>
    <el-input v-if="!studyMode" @blur="handleBlur" class="input" placeholder="输入翻译内容" type="textarea" rows="2" v-model="currentVal"/>
    <el-button type="primary" size="mini" @click="prev" v-if="currentIndex > 0" icon="el-icon-arrow-left">上一题</el-button>
    <el-button type="primary" size="mini" @click="next" v-if="currentIndex < len">下一题<i class="el-icon-arrow-right el-icon--right"></i></el-button>
    <el-button v-if="!studyMode" type="primary" size="mini" @click="showAns = !showAns">{{ showAns ? '隐藏答案' : '查看答案' }}</el-button>
    <p>
      <el-input align="center" class="jump-input" type="text" size="mini" v-model="targetIndex"></el-input>
      <el-button class="jump-btn" type="primary" size="mini" @click="jump">GO! </el-button>
    </p>
    <p v-if="showAns">{{current['zh-cn']}}</p>
  </div>
</template>

<script>
import translate from '@/data/translate'
import strSimilarity2Percent from '@/util'

export default {
  data () {
    return {
      currentIndex: 0,
      currentVal: null,
      showAns: false,
      percent: null,
      len: null,
      targetIndex: 1,
      lastIndex: null,
      studyMode: false
    }
  },

  methods: {
    next () {
      this.currentIndex++
    },
    prev () {
      this.currentIndex--
    },
    jump () {
      let target = this.targetIndex - 1
      if (target >= 0 && target < this.len) {
        this.currentIndex = target
      }
    },
    proceed () {
      this.currentIndex = this.lastIndex
    },
    handleBlur () {
      if (this.percent >= .8) {
        this.$message({
          duration: 1000,
          type: 'success',
          message: '回答正确，自动进入下一题',
          offset: 0
        })
        this.next()
      }
    }
  },

  filters: {
    showPercent (val) {
      return Number(val * 100).toFixed(2) + '%'
    }
  },

  computed: {
    current () {
      return translate.list[this.currentIndex]
    },
    showLastIndex () {
      return this.lastIndex && (this.currentIndex !== this.lastIndex)
    }
  },

  watch: {
    currentVal (val) {
      if (!val) return
      this.percent = strSimilarity2Percent(val, this.current['zh-cn'])
      if (val[val.length - 1] === '\n') {
        this.handleBlur()
      }
    },
    currentIndex () {
      this.lastIndex = null
      this.currentVal = null
      this.showAns = false
      this.percent = null
      this.targetIndex = parseInt(this.currentIndex) + 1
      localStorage.setItem('lastIndex', this.currentIndex)
    }
  },

  mounted () {
    this.len = translate.list.length
    const lastIndex = localStorage.getItem('lastIndex')
    if (lastIndex) {
      this.lastIndex = parseInt(lastIndex)
    }
  }
}
</script>

